<template>
  <div class="content">    
      <el-table :data="tableData"  border style="width: 100%;margin-bottom: 20px;" ref="filterTable" >
        <el-table-column
          prop="AdminName"
          align="center"
          :show-overflow-tooltip="true"
          label="操作人"
        ></el-table-column>
        <el-table-column
          prop="ApiPath"
          align="center"
          :show-overflow-tooltip="true"
          label="操作接口"
        ></el-table-column>
        <el-table-column prop="Ip" align="center" label="IP" ></el-table-column>
        <el-table-column prop="CreatedAt" align="center" label="时间" ></el-table-column>
      </el-table>
      <el-pagination
        layout="total, prev, pager, next, jumper"
        :total="page.totle"
        :page-size="page.Limit"
        :current-page.sync="page.page"
        @current-change="handleCurrentPage"
        style="text-align: right;margin-top: 20px;"
      ></el-pagination>

  </div>
</template>
<script>
export default {
  name: "recordList",
  data() {
    return {
      tableData: [],
      page:{
        page:1,
        Limit:20,
        totle:0,
      },
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    //获取用户列表以及全部角色
    loadData() {
      let params = {
        Page:this.page.page,
        Limit:this.page.Limit,
      }
      this.apiSever.get(`${this.URl.RECORD_LIST+'?Page='+params.Page+'&Limit='+params.Limit}`, data => {
        this.tableData = data.data.list;
        this.page.totle =data.data.count;
      });
    },
    //点击其他页面
    handleCurrentPage(val) {
      this.page.page = val;
      this.loadData();
    },
    
  }
};
</script>
<style lang="less" scoped>
.content{
    margin-top: 0px;
    padding: 20px 20px;
    background-color: #fff;
}
</style>
